    body {
        margin: 0;
        background-color: #ffffff;
    }
    
    a {
        text-decoration: none;
        color: #ffffff;
    }
    
    ul {
        padding: 0;
        margin: 0;
    }
    
    li {
        list-style: none;
    }
    
    button {
        cursor: pointer;
        outline: none;
        border: none;
    }
    
    *,
    *:before,
    *:after {
        box-sizing: border-box;
        -webkit-box-sizing: border-box;
    }
    
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0;
        font-weight: 500;
    }
    
    input {
        outline: none;
        cursor: pointer;
    }